<template>
  <link rel="preconnect" href="https://fonts.googleapis.com">
  <link rel="preconnect" href="https://fonts.gstatic.com" crossorigin>
  <link href="https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap" rel="stylesheet">

  <div class="black-bg" v-if="OnOpenModal">
    <div class="white-bg">
      <h4>답</h4>
      <p> {{result}} ㅁㄴㅇㄴㅇㅁ</p>
      <input style=" font-size: 20px ;height: 40px; width: 100%; border-radius: 20px; border-color: black; background-color: rgb(179, 255, 204) " @click="OnOpenModal=false" type="button" value="닫기">
    </div>
  </div>
  <form>
    <div class="input">
      <input style="width: 60%; height: 50px; font-size: 20px; margin-top: 30px; border-radius: 20px; border-color: black;" @change="Queston = SetQueston($event)" type="text" placeholder="질문 작성">
    </div>
    <div class="input">
      <input style="width: 60%; height: 50px; font-size: 20px; margin-top: 30px; border-radius: 20px; border-color: black; background-color: rgb(179, 255, 204) " @click="result = ResultFunction(Queston), OnOpenModal= true" type="button" value="제출">
    </div>
  </form>
</template>

<script>

export default {
  name: 'App',
  data(){
    return{
      OnOpenModal: false,
      Queston : null,
      result: null
    }
  },
  methods : {
    SetQueston(event) {
      return event.target.value;
    },
    ResultFunction(Q){
      if (Q === "최예원은 예쁘다" || Q === "최예원은 귀엽다" || Q === "최예원은 똑똑하다") {
        return "지랄 ㄴ"
      } else if (Q === "승환이는 똑똑하다") {
        return "ㅇㅈ"
      } else {
        return "데이터가 읎음 ㅠ"
      }
    }
  },
  components: {
  }
}
</script>

